// react-redux
import { useDispatch, useSelector } from "react-redux";

// mui-components
import { Box, Button } from "@mui/material";

// mui-icons
import DownloadRoundedIcon from '@mui/icons-material/DownloadRounded';
import DeleteIcon from '@mui/icons-material/Delete';

// utils
import { deleteObjectStore, downloadObjectStore } from "../../utils/utils";

// constants
import { DARK_RED, LIGHT_RED, UPSET_FACE } from "../../constants/constants";

// sweetalert
import swal from "sweetalert";

export const Folder = ({folder}) => {
    const dispatch = useDispatch();

    const folderLength = useSelector(state => state.photos.elems[folder].length);

    const folderContainer = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-start',
        margin: '6px 8px',
    };

    const folderBtn = {
        padding: '6px 12px',       
    };

    const deleteIcon = {
        fontSize: '20px',
        color: LIGHT_RED,
        marginLeft: '7px',
        cursor: 'pointer',
        transition: 'color 250ms ease',
        ':hover': {
          color: DARK_RED,
        }
    };

    const downloadIcon = {
        fontSize: '20px',
        color: '#eda21a',
        marginLeft: '7px',
        cursor: 'pointer',
        transition: 'color 250ms ease',
        ":hover": {
            color: '#ab7833',
        }
    };

    const onDeleteBtnClicked = () => {
        deleteObjectStore({folder, dispatch});
    };

    const onDownloadBtnClicked = e => {
        if (!folderLength) {
            swal({
                title: "Oops!",
                text: `Failed to download: the folder "${folder}" is empty.`,
                icon: "error",
                button: `Oh, OK ${UPSET_FACE}`,
            });

            return;
        }
        
        downloadObjectStore({folder});
    };

    return <Box sx={folderContainer}>
    <Button sx={folderBtn} variant="contained" className='photosTypeBtn' size="small">{folder}</Button>
    <DownloadRoundedIcon sx={downloadIcon} onClick={onDownloadBtnClicked} />
    <DeleteIcon sx={deleteIcon} onClick={onDeleteBtnClicked} />
  </Box>
};