// react
import { useState } from "react";

export const useDelay = delay => {
    const [timeID, setTimeID] = useState(null);

    return [
        timeID,
        function() {
            if (timeID !== null) {
                setTimeID( clearTimeout(timeID) );
            }
        
            setTimeID( setTimeout( () => {
                setTimeID(null);
              } , delay ) );
        }
    ]
};