// react
import { useEffect, useState } from "react";

// react-redux
import { useDispatch, useSelector } from "react-redux";

// components
import { DrawerNavigator } from "../DrawerNavigator/DrawerNavigator";
import { WarningAlert } from '../WarningAlert/WarningAlert';

// custom hooks
import { useDelay } from "../../hooks/useDelay";

// mui-components
import { Box, Button, Drawer, Typography } from "@mui/material";
import Tooltip from '@mui/material/Tooltip';

// mui-icons
import HolidayVillageIcon from '@mui/icons-material/HolidayVillage';
import AddAPhotoIcon from '@mui/icons-material/AddAPhoto';
import DeleteIcon from '@mui/icons-material/Delete';
import CreateNewFolderIcon from '@mui/icons-material/CreateNewFolder';
import MenuOpenIcon from '@mui/icons-material/MenuOpen';

// utils
import { initApp, uploadImage } from "../../utils/utils";

// actions
import { setDeleteMode, setSelectedPhotosType } from "../../redux/slices/photosSlice";

// constants
import { BLACK, BODY_COLOR, HEADER_COLOR, LIGHT_BODY_COLOR, LIGHT_TELEGRAY, TELEGRAY } from '../../constants/constants';

// mui-font
import '@fontsource/roboto/700.css';

export const Header = () => {
    const dispatch = useDispatch();

    const wasInitialized = useSelector(state => state.photos.wasInitialized);
    const selectedPhotosType = useSelector(state => state.photos.selectedPhotosType);
    const isDeleteMode = useSelector(state => state.photos.isDeleteMode);
    const filesCount = useSelector(state => state.photos.elems[selectedPhotosType]?.length);

    const [drawerVisibility, toggleVisibility] = useState(false);
    const [drawerMenuVisibility, setMenuVisibility] = useState(false);
    const [isEnter, setIsEnter] = useState(false);
    const [filteredLength, setLength] = useState(0);

    useEffect(() => initApp({wasInitialized, dispatch}), []);

    const [uploadedTimeID, showUploadedAlert] = useDelay(7500);
    const [alreadyUploadedTimeID, showAlreadyUploadedAlert] = useDelay(7500);

    const [alreadyUploadedText, setAlreadyUploadedText] = useState('');

    const upload = e => {
        setLength(0);
        const input = e.target;

        const files = Object.values(input.files);

        // if is not image
        const filtered = files.filter(file => file.type.split('/')[0].toLowerCase() !== 'image');

        if (filtered.length) {
            setLength(filtered.length);
            showUploadedAlert();
            
            input.value = '';
            return;
        }

        const result = [];

        // remove copies from uploaded images
        for (let i = 0; i < files.length; i++) {
            const found = result.find(file => file.name === files[i].name);

            if (found) continue;

            result.push(files[i]);
        }

        uploadImage({photos: files.length > 1 ? result : files, dispatch, selectedPhotosType, showAlreadyUploadedAlert, setAlreadyUploadedText});
        setMenuVisibility( false );
        input.value = '';
    };

    const onHomeBtnClicked = () => {
        dispatch( setSelectedPhotosType(null) );
        setMenuVisibility( false );
    };

    const header = {
        position: 'relative',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        top: '0',
        left: '0',
        width: '100%',
        padding: '7px 0',
        backgroundColor: HEADER_COLOR,
        textAlign: 'center',
        zIndex: '999',
        '@media screen and (max-width: 768px)': {
            justifyContent: 'center',
        }
    };

    const leftIconsContainer = {
        display: 'flex',
        flexDirection: 'row',
        flexWrap: 'nowrap',
        alignItems: 'center',
        justifyContent: 'center',
        marginLeft: '10px',
        '@media screen and (max-width: 768px)': {
            display: 'none',
        },
    };

    const folderIcon = {color: HEADER_COLOR, fontSize: '24px'};

    const folderBtn = {
      backgroundColor: BODY_COLOR,
      ':hover': {
          backgroundColor: LIGHT_BODY_COLOR,
      },
    };

    const uploadBtnContainer = {
        marginRight: '10px',
        visibility: `${selectedPhotosType ? 'visible' : 'hidden'}`,
        '@media screen and (max-width: 768px)': {
            display: 'none',
        },
        whiteSpace: 'nowrap',
    };

    const drawerUploadBtnContainer = {
        visibility: `${selectedPhotosType ? 'visible' : 'hidden'}`,
        '@media screen and (max-width: 768px)': {
            display: 'block',
        },
    };

    const uploadBtn = {
        fontSize: '14px',
        '@media screen and (max-width: 1024px)': {
            fontSize: '12px',
        },
    };

    const villageIcon = {
        color: HEADER_COLOR,
        fontSize: '24px',
    };

    const btnDimensions = {
        width: '50px',
        height: '36px',
        padding: '6px 8px',
        marginLeft: '4px',
        '@media screen and (max-width: 768px)': {
            width: '42px',
            height: '30px',
            padding: '3px 5px',
            backgroundColor: BODY_COLOR,
            ':hover': {
                backgroundColor: LIGHT_BODY_COLOR
            },
        }
    };

    const iconDimensions = {
        fontSize: '24px',
        '@media screen and (max-width: 768px)': {
            fontSize: '18px',
        }
    };

    const homeBtn = {
        backgroundColor: BODY_COLOR,
        ':hover': {
            backgroundColor: LIGHT_BODY_COLOR
        },
        ':disabled': {
            backgroundColor: BLACK,
        }
    };

    const deleteIcon = {
        color: HEADER_COLOR,
        fontSize: '24px', 
    };

    const menuOpenIcon = {
        position: 'absolute',
        cursor: 'pointer',
        top: '50%',
        transform: 'translateY(-50%)',
        left: '15px',
        color: LIGHT_TELEGRAY,
        fontSize: '36px',
        display: 'none',
        transition: 'color 200ms ease',
        zIndex: '999',
        ':hover': {
            color: LIGHT_BODY_COLOR,
        },
        '@media screen and (max-width: 768px)': {
            display: 'block',
        },
        '@media screen and (max-width: 550px)': {
            fontSize: '32px',
            left: '8px',
        },
    };

    const deleteBtn = {
        backgroundColor: isDeleteMode ? TELEGRAY : BODY_COLOR,
        ':hover': {
            backgroundColor: isDeleteMode ? LIGHT_TELEGRAY : LIGHT_BODY_COLOR,
        },
        ':disabled': {
            backgroundColor: BLACK,
        },
    };

    const mainTitle = {
        position: 'relative',
        left: '0',
        padding: '0 25px',
        maxWidth: '900px',
        whiteSpace: 'nowrap',
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        fontSize: '48px',
        textShadow: `${LIGHT_BODY_COLOR} 4px 3px 4px, ${LIGHT_BODY_COLOR} -4px -3px 4px, ${LIGHT_BODY_COLOR} 4px -3px 4px, ${LIGHT_BODY_COLOR} -4px 3px 4px`,
        '@media screen and (max-width: 1440px)': {
            maxWidth: '700px',
        },
        '@media screen and (max-width: 1280px)': {
            fontSize: '42px',
            maxWidth: '500px',
        },
        '@media screen and (max-width: 1024px)': {
            fontSize: '42px',
            maxWidth: '320px',
        },
        '@media screen and (max-width: 768px)': {
            fontSize: '42px',
            maxWidth: '380px',
        },
        '@media screen and (max-width: 550px)': {
            fontSize: '36px',
            maxWidth: '240px',
        },
        '@media screen and (max-width: 400px)': {
            fontSize: '32px',
            maxWidth: '215px',
        }
    };

    const addPhotoIcon = {
        marginLeft: '10px',
        fontSize: '24px',
        color: 'white',
        '@media screen and (max-width: 1024px)': {
            fontSize: '20px',
        },
        '@media screen and (max-width: 768px)': {
            fontSize: '18px',
            color: BLACK,
            margin: '0',
        }
    };

    const drawer = {
        '@media screen and (min-width: 769px)': {
            display: 'none',
        }
    };

    const drawerMenu = {
        position: 'relative',
        top: '0',
        left: '0',
        width: 'fit-content',
        height: '100%',
        padding: '10px',
        backgroundColor: '#111'
    };

    const onFolderBtnClicked = () => {
        toggleVisibility(true);
        setIsEnter(false); 
      };
  
      const onFolderBtnEnter = () => setIsEnter(true);
      const onFolderBtnLeave = () => setIsEnter(false);

    const onDeleteBtnClicked = () => {
        dispatch( setDeleteMode( !isDeleteMode ) );
        setMenuVisibility(false);
    };

    const onMenuIconClicked = () => setMenuVisibility(true);

    const onDrawerMenuClose = () => setMenuVisibility(false);

    return (
        <Box component='header' sx={header}>
            <MenuOpenIcon sx={menuOpenIcon} onClick={onMenuIconClicked} />
            <Drawer sx={drawer} anchor="left" open={drawerMenuVisibility} onClose={onDrawerMenuClose}>

            <Box sx={drawerMenu}>
                <Box>
                    <Button variant="text" sx={{...folderBtn, ...btnDimensions, margin: '5px'}} onClick={() => toggleVisibility(true)}>
                        <CreateNewFolderIcon sx={{...folderIcon, ...iconDimensions}} />
                    </Button>
                </Box>               

                <Box>
                    <Button sx={{...homeBtn, ...btnDimensions, margin: '5px'}} onClick={onHomeBtnClicked} variant='text' disabled={!selectedPhotosType}>
                        <HolidayVillageIcon sx={{...villageIcon, ...iconDimensions}} />
                    </Button>
                </Box>

                <Box>
                    <Button sx={{...deleteBtn, ...btnDimensions, margin: '5px'}} className="headerDeleteBtn" disabled={!selectedPhotosType || !filesCount} onClick={onDeleteBtnClicked}>
                        <DeleteIcon sx={{...deleteIcon, ...iconDimensions}} className="headerDeleteIcon" />
                    </Button>
                </Box>
                <Box sx={drawerUploadBtnContainer}>
                    <Button sx={{...uploadBtn, ...btnDimensions, margin: '5px'}} variant="contained" component='label'>
                        <input type='file' onChange={upload} hidden multiple />
                        <AddAPhotoIcon sx={addPhotoIcon} />
                    </Button>
                </Box>

            </Box>

            </Drawer>
            <Box sx={leftIconsContainer}>
                <DrawerNavigator drawerVisibility={drawerVisibility} toggleVisibility={toggleVisibility} setMenuVisibility={setMenuVisibility} />
                <Tooltip title='Folders' open={isEnter}>
                    <Box>
                        <Button variant="text" sx={{...folderBtn, ...btnDimensions}} onClick={onFolderBtnClicked} onMouseEnter={onFolderBtnEnter} onMouseLeave={onFolderBtnLeave}>
                            <CreateNewFolderIcon sx={{...folderIcon, ...iconDimensions}} />
                        </Button>
                    </Box>
                </Tooltip>                               
                <Tooltip title={selectedPhotosType ? 'Home' : ''}>
                    <Box>
                        <Button sx={{...homeBtn, ...btnDimensions}} onClick={onHomeBtnClicked} variant='text' disabled={!selectedPhotosType}>
                            <HolidayVillageIcon sx={{...villageIcon, ...iconDimensions}} />
                        </Button>
                    </Box>
                </Tooltip>
                <Tooltip title={!selectedPhotosType || !filesCount ? '' : 'Delete mode'}>
                    <Box>
                        <Button sx={{...deleteBtn, ...btnDimensions}} className="headerDeleteBtn" disabled={!selectedPhotosType || !filesCount} onClick={onDeleteBtnClicked}>
                            <DeleteIcon sx={{...deleteIcon, ...iconDimensions}} className="headerDeleteIcon" />
                        </Button>
                    </Box>
                </Tooltip>
            </Box>
            <Typography sx={mainTitle} color='white' variant="h3" component='h1'>
                { selectedPhotosType ? selectedPhotosType[0].toUpperCase() + selectedPhotosType.slice(1).toLowerCase() : 'Photo gallery.' }
            </Typography>
            <Box sx={uploadBtnContainer}>
                <Button sx={uploadBtn} variant="contained" component='label'>
                    Upload photos
                    <input type='file' onChange={upload} hidden multiple />
                    <AddAPhotoIcon sx={addPhotoIcon} />
                </Button>
            </Box>

            <WarningAlert text={`Failed to upload: ${filteredLength > 1 ? 'files are not images' : 'file is not an image'}.`} from='left' needToShow={uploadedTimeID !== null} />
            <WarningAlert text={alreadyUploadedText} from='left' needToShow={alreadyUploadedTimeID !== null} />
        </Box>
    )
};