// react
import { useEffect, useRef } from "react";

// mui-components
import { Alert } from "@mui/material";

// mui-icons
import ErrorIcon from '@mui/icons-material/Error';

export const WarningAlert = ({text, needToShow, from = 'left', styles = {}}) => {
    const alertStyles = {
        position: 'absolute',
        display: 'flex',
        alignItems: 'flex-start',
        justifyContent: 'center',
        top: '10vh', 
        left: from === 'left' ? '0' : '100vw', 
        zIndex: '1',
        textAlign: 'center',
        color: '#adadad',
        boxShadow: '0px 0px 16px white',
        backgroundColor: '#0f0c1a',
        minWidth: '250px',
        maxWidth: '500px',
        maxHeight: '60vh',
        overflowX: 'hidden',
        overfloY: 'auto',
        width: '50vw',
        transition: 'transform 250ms cubic-bezier(0.18, 0.03, 0.4, 1.14)',
        transform: from === 'left' ? 'translateX(-120%)' : 'translateX(50%)',
      };

      const ref = useRef(null);

      useEffect(() => {
        if (ref.current !== null) {

            if (from === 'left') {
                ref.current.style.transform = `${needToShow ? 'translateX(0)' : 'translateX(-120%)'}`
            } else if (from === 'right') {
                ref.current.style.transform = `${needToShow ? 'translateX(-100%)' : 'translateX(50%)'}`
            }

        }
      }, [needToShow]);

    return <Alert ref={ref} severity="warning" sx={{...alertStyles, ...styles}} icon={ <ErrorIcon /> }>{text}</Alert>
};