// react
import { useState } from "react";

// react-redux
import { useDispatch, useSelector } from "react-redux";

// components
import { NewTypeWindow } from "../NewTypeWindow/NewTypeWindow";
import { Folder } from "../Folder/Folder";

// mui-components
import { Box, Button, Drawer } from "@mui/material";

// mui-icons
import DownloadRoundedIcon from '@mui/icons-material/DownloadRounded';
import AddIcon from '@mui/icons-material/Add';

// actions
import { setSelectedPhotosType } from "../../redux/slices/photosSlice";

// utils
import { downloadAllObjectStores } from "../../utils/utils";

// uuid
import uuid from "react-uuid";

export const DrawerNavigator = ({drawerVisibility, toggleVisibility, setMenuVisibility}) => {
    const [addPhotosTypeBtnIsClicked, setIsClicked] = useState(false);
  
    const filesCount = useSelector(state => state.photos.filesCount);
    const folders = useSelector(state => Object.keys(state.photos.elems));
    const selectedPhotosType = useSelector(state => state.photos.selectedPhotosType);

    const dispatch = useDispatch();
  
    const onDrawerClicked = ({target}) => {
      if (!target.classList.contains('photosTypeBtn')) return;

      const type = target.innerText.toLowerCase();

      if (type === selectedPhotosType) {
        toggleVisibility( false );
        setMenuVisibility( false );
        return;
      }

      dispatch( setSelectedPhotosType( type ) );
      toggleVisibility( false );
      setMenuVisibility( false );
    };

    const openNewTypeWindow = () => {
      setIsClicked(true);
    };

    const btnStyles = {padding: '6px 12px', margin: '6px 12px'};

    const onDownloadAllBtnClicked = () => {
      downloadAllObjectStores();
    };

    const onClose = () => toggleVisibility(false);

    return (
        <Box>
        <Drawer onClick={onDrawerClicked} anchor='left' open={drawerVisibility} onClose={onClose}>

          {
            folders?.map(folder => <Folder key={uuid()} folder={folder} />)
          }
          {
            filesCount > 0 && folders.length > 1 && (
              <Button sx={btnStyles} onClick={onDownloadAllBtnClicked} variant="contained" size="small">
                <DownloadRoundedIcon />
              </Button>
            )
          }
          <Button sx={btnStyles} onClick={openNewTypeWindow} variant="outlined" className='addPhotosType' size="small">
            <AddIcon />
          </Button>  
        </Drawer>
        <NewTypeWindow addPhotosTypeBtnIsClicked={addPhotosTypeBtnIsClicked} toggleVisibility={toggleVisibility} setMenuVisibility={setMenuVisibility} setIsClicked={setIsClicked} />
      </Box>
    );
};