// react
import { useState } from "react";

// react-redux
import { useDispatch, useSelector } from "react-redux";

// mui-components & hooks
import { Box, TextField, useMediaQuery } from "@mui/material";

// mui-icons
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';

// utils
import { deleteFromObjectStore, renamePhotoFromObjectStore } from "../../utils/utils";

// constants
import { HEADER_COLOR } from "../../constants/constants";

// mui-styles
import styled from "@emotion/styled";

export const Photo = ({fullName, isActive, url, id, isDeleteMode, rerender, showRenamedAlert, setNameOnError}) => {
    const selectedPhotosType = useSelector(state => state.photos.selectedPhotosType);
    
    const name = fullName.slice(0, fullName.indexOf('.'));

    const [val, setVal] = useState('');
    const [isFocused, setIsFocused] = useState(false);

    const dispatch = useDispatch();

    const is1440px = useMediaQuery('@media screen and (max-width: 1440px)');
    const is768px = useMediaQuery('@media screen and (max-width: 768px)');

    const onFocus = e => {
        setIsFocused(true);
        setVal(e.target.value);
    };

    const onBlur = ({target}) => {
        const newName = target.value;

        // if focused and didn't change photo name
        if (newName === name) {
            setIsFocused(false);
            rerender( Date.now() );
            return;
        }

        // if photo name is empty
        if (!newName.length) {
            setVal('');
            setIsFocused(false);
            rerender( Date.now() );
            return;
        }        

        renamePhotoFromObjectStore({newName, storeName: selectedPhotosType, dispatch, id, setNameOnError, showRenamedAlert});

        setIsFocused(false);
        rerender( Date.now() );
    };

    const inputProps = {
        inputProps: {
            style: {
                textAlign: 'center',
                color: HEADER_COLOR,
                textShadow: 'black 1px 1px 1px',
                fontSize: is768px ? '14px' : is1440px ? '16px' : '18px',
            },
        }
    };

    const onRemoveIconClicked = () => {
        deleteFromObjectStore({id, dispatch, selectedPhotosType});
    };

    const onKeyUp = ({target, code, key}) => {
        if (code === 'Enter' || key === 'Enter') {
            const newName = target.value;

            // if focused and didn't change photo name
            if (newName === name) {
                setIsFocused(false);
                rerender( Date.now() );
                return;
            }
    
            // if photo name is empty
            if (!newName.length) {
                setVal('');
                setIsFocused(false);
                rerender( Date.now() );
                return;
            }
    
            renamePhotoFromObjectStore({newName, storeName: selectedPhotosType, dispatch, id, setNameOnError, showRenamedAlert});
    
            setIsFocused(false);
            rerender( Date.now() );
        }
    };

    const photoContainer = {
        position: 'relative',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
        margin: '16px 18px',
        userSelect: 'none',
    };

    const notSelected = {
        filter: 'blur(6px) grayscale(0.75) contrast(0.5)',
    };

    const highlightImage = isActive ? {
        filter: 'blur(0px) grayscale(0) contrast(1)',
        boxShadow: '0px 0px 5vh #959595',
    } : {};

    const deleteModeAnim = isDeleteMode ? {
        animation: 'deleteAnim .11s ease-in alternate infinite',
        '@keyframes deleteAnim': {
            '0%': {
                transform: 'rotate(-1deg)',
            },
            '100%': {
                transform: 'rotate(1deg)',
            },
          },
    } : {};

    const Image = styled('img')({
        maxWidth: '70vw',
        maxHeight: '70vh',
        minWidth: '200px',
        objectFit: 'cover',
        borderRadius: '2px',
        '@media screen and (max-height: 500px) and (min-width: 450px)': {
            maxWidth: '55vw',
            maxHeight: '55vh',
        },
        '@media screen and (max-width: 500px)': {
            minWidth: '100px',
        }
    });

    const imageContainer = {
        position: 'relative',
        marginBottom: '6px',
        objectFit: 'cover',
    };

    const removeIcon = {
        position: 'absolute', 
        fontSize: '32px', 
        left: 'calc(100% - 16px)',
        top: '-16px', 
        color: '#888',
        cursor: 'pointer',
        '@media screen and (max-width: 1024px)': {
            fontSize: '28px', 
            left: 'calc(100% - 14px)', 
            top: '-14px',             
        },
        '@media screen and (max-width: 768px)': {
            fontSize: '24px', 
            left: 'calc(100% - 12px)', 
            top: '-12px',             
        }
    };

    const onTextFieldChange = e => setVal(e.target.value);

    return <Box sx={photoContainer} className='photoContainer' onKeyUp={onKeyUp}>
        <Box sx={{...imageContainer, ...notSelected, ...highlightImage, ...deleteModeAnim}}>
            <Image alt={fullName} src={url} />
            {
                isDeleteMode && isActive && <RemoveCircleIcon sx={removeIcon} onClick={onRemoveIconClicked} />
            }
        </Box>
        <TextField disabled={!isActive} error={isFocused && val.length === 0} value={isFocused ? val : name} onChange={onTextFieldChange} 
        variant='standard' onFocus={onFocus} onBlur={onBlur}
        InputProps={inputProps} fullWidth={true} autoFocus={isFocused}></TextField>
    </Box>
};