// react
import { useEffect, useState } from "react";

// react-redux
import { useDispatch, useSelector } from "react-redux";

// mui-components
import { Avatar, Box, Button, List, ListItem, ListItemAvatar, ListItemText, Typography, useMediaQuery } from "@mui/material";
import Tooltip from '@mui/material/Tooltip';

// chart
import { Chart as ChartJS, ArcElement, Tooltip as ChartTooltip, Legend } from 'chart.js';
import { Doughnut } from 'react-chartjs-2';

// utils
import { getStorageInfo } from "../../utils/utils";

// actions
import { setFilesCount, setSelectedPhotosType, setStorageInfo } from "../../redux/slices/photosSlice";

// constants
import { HEADER_COLOR, LIGHT_BODY_COLOR, LIGHT_GRAY, WHITE } from "../../constants/constants";

// uuid
import uuid from 'react-uuid';

// mui-icons
import FolderIcon from '@mui/icons-material/Folder';

export const MainInfo = () => {
    const dispatch = useDispatch();

    const foldersCount = useSelector(state => Object.keys(state.photos.elems).length);
    const quota = useSelector(state => ( +(state.photos.storageInfo.quota / 1024 / 1024).toFixed(2)) );
    const usage = useSelector(state => ( +(state.photos.storageInfo.usage/ 1024 / 1024).toFixed(2) ) );

    const is768px = useMediaQuery('@media screen and (max-width: 768px)');

    const [height, setHeight] = useState(0);

    useEffect(() => {
        setHeight( Math.round(document.querySelector('header').getBoundingClientRect().height) );
    }, []);

    const {foldersLength, wholeLength} = useSelector(state => {
        const folders = Object.entries(state.photos.elems);

        const foldersLength = [];
        let wholeLength = 0;

        for (const [key, value] of folders) {
            foldersLength.push({name: key, length: value.length});
            wholeLength += value.length;
        }

        return {foldersLength, wholeLength};
    });

    useEffect(() => {
        dispatch( setFilesCount(wholeLength) );
    }, [wholeLength]);

    useEffect(() => {
        getStorageInfo(dispatch, setStorageInfo);
    }, []);

    ChartJS.register(ArcElement, ChartTooltip, Legend);

    const data = {
        labels: ['Storage memory', 'Memory usage'],
        datasets: [
          {
            label: 'Total, MB',
            data: [quota, usage],
            backgroundColor: [
                HEADER_COLOR,
                LIGHT_BODY_COLOR
            ],
            borderColor: [
                LIGHT_BODY_COLOR,
                HEADER_COLOR
            ],
            borderWidth: 1,
          },
        ],
      };

    const onFoldersListItemClicked = (e) => {
        let target;

        if (e.target.tagName !== 'LI') {
            target = e.target.closest('li');
        }

        if (!target) return;

        const folderName = target.children[0].children[1].children[0].innerText.toLowerCase();

        dispatch( setSelectedPhotosType( folderName ) );
    };

    const mainInfoContainer = {
        position: 'relative',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
        top: '0',
        left: '0',
        margin: '0',
        padding: '5px 30px',
        width: 'calc(100% - 60px)',
        minHeight: `calc(100vh - ${height + 10}px)`,
    };

    const noTypeTitle = {
        position: 'relative',
        color: 'white',
        textAlign: 'center',
        fontSize: '48px',
        textShadow: `${HEADER_COLOR} 10px 10px 5px`,
        '@media screen and (max-width: 1024px)': {
            fontSize: '38px',
        },
        '@media screen and (max-width: 768px)': {
            fontSize: '32px',
            textShadow: `${HEADER_COLOR} 8px 8px 4px`,
        },
        '@media screen and (max-width: 550px)': {
            fontSize: '28px',
        },
        '@media screen and (max-width: 400px)': {
            fontSize: '24px',
            textShadow: `${HEADER_COLOR} 6px 6px 3px`,
        }
    };

    const foldersList = {
        margin: '10px 0 0 0',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexWrap: 'wrap',
    };

    const foldersListItem = {
        margin: '10px 15px',
        width: 'fit-content',
        height: 'fit-content',
        transition: "background-color 250ms ease, opacity 250ms ease",
        '@media screen and (max-width: 768px)': {
            margin: '6px 9px',
        }
    };

    const button = {borderRadius: '8px'};

    const folderAvatar = {
        backgroundColor: HEADER_COLOR,
        width: '40px',
        height: '40px',
        '@media screen and (max-width: 768px)': {
            width: '32px',
            height: '32px',
        }
    };

    const folderAvatarIcon = {
        color: WHITE,
        fontSize: '24px',
        '@media screen and (max-width: 768px)': {
            fontSize: '20px',
        }
    };

    const doughnutChart = {
        width: '300px',
        height: '300px',
        '@media screen and (max-width: 1024px)': {
            width: '270px',
            height: '270px',
        },

        '@media screen and (max-width: 768px)': {
            width: '230px',
            height: '230px',
        }
    };

    const primaryTypographyProps = {
        color: WHITE,
        fontSize: is768px ? '13px' : '16px',
    };

    const secondaryTypographyProps = {
        color: LIGHT_GRAY,
        fontSize: is768px ? '11px' : '14px',
    };

    const text = `${foldersCount ? foldersCount > 1 ? 'You already have ' + foldersCount + ' folders with ' + (wholeLength ? wholeLength : 'no') + (wholeLength === 1 ? ' file.' : ' files.') + ' Open any to see or upload your images' :
    'You already have ' + foldersCount + ' folder with ' + (wholeLength ? wholeLength : 'no') + (wholeLength === 1 ? ' file.' : ' files.') + ' Open it to see or upload your images' :
    'Create a folder to upload your images. To do that, click on the icon located in the upper left corner.'}`;
    
    return <Box sx={mainInfoContainer}>
        <Typography sx={noTypeTitle} component='h3' variant='h3'>{text}</Typography>
        <List sx={foldersList} onClick={onFoldersListItemClicked}>
            {
            foldersLength?.map(folder => {
                const name = folder.name[0].toUpperCase() + folder.name.slice(1);
                const length = folder.length === 1 ? folder.length + ' file' : folder.length + ' files';
                return <ListItem sx={foldersListItem} key={uuid()}>
                    <Tooltip title={`Go to ${name}`}>
                        <Button sx={button}>
                        <ListItemAvatar sx={{minWidth: '0px', marginRight: '8px'}}>
                            <Avatar sx={folderAvatar}>
                                <FolderIcon sx={folderAvatarIcon} />
                            </Avatar>
                        </ListItemAvatar>
                        <ListItemText primary={name} secondary={length} primaryTypographyProps={primaryTypographyProps} secondaryTypographyProps={secondaryTypographyProps} />
                        </Button>
                    </Tooltip>
                </ListItem>
                })
            }
        </List>
        {
            usage > 0 && wholeLength > 0 && (
                <Box sx={doughnutChart}>
                    <Doughnut data={data} />
                </Box>
            )
        }        
    </Box>
};