// react
import { useState } from "react";

// react-redux
import { useSelector } from "react-redux";

// components
import { Photo } from "../Photo/Photo";
import { WarningAlert } from '../WarningAlert/WarningAlert';

// swiper components
import { Swiper, SwiperSlide } from 'swiper/react';
import { EffectCoverflow, Virtual, Mousewheel, Keyboard, FreeMode } from 'swiper';

// mui-components
import { Box, Typography, useMediaQuery } from "@mui/material";

// custom hooks
import { useDelay } from '../../hooks/useDelay';

// uuid
import uuid from 'react-uuid';

// constants
import { HEADER_COLOR } from "../../constants/constants";

// mui-styled
import styled from "@emotion/styled";

// swiper styles
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/virtual';

let index = 0;

export const Photos = () => {    
    const selectedPhotosType = useSelector(state => state.photos.selectedPhotosType);
    const photos = useSelector(state => state.photos.elems[selectedPhotosType]) || [];
    const isDeleteMode = useSelector(state => state.photos.isDeleteMode);

    const is1440px = useMediaQuery('@media screen and (max-width: 1440px)');
    const is1024px = useMediaQuery('@media screen and (max-width: 1024px)');
    const is768px = useMediaQuery('@media screen and (max-width: 768px)');

    const [renamedTimeID, showRenamedAlert] = useDelay(7500);
    const [nameOnError, setNameOnError] = useState('');

    const [_, rerender] = useState(0);

    const imgMainContainer = {
        position: 'relative',
        width: '100vw',
        height: 'calc(100vh - 70px)',
        margin: '0',
        padding: '0',
        overflow: 'hidden',
    };

    const noPhotosContainer = {
        position: 'relative',
        top: '0',
        left: '0',
        width: '100%',
        height: 'calc(100vh - 70px)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    };

    const PhotoSwipper = styled(Swiper)({
        width: '100%',
        height: '100%',
        padding: '0',
        position: 'relative',
        background: 'transparent',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    });

    const PhotoSlide = styled(SwiperSlide)({
        alignItems: 'center',
        justifyContent: 'center',
        display: 'flex',
        background: 'transparent',
    });

    const noPhotosTitle = {
        position: 'relative',
        color: 'white',
        textAlign: 'center',
        fontSize: '48px',
        padding: '0 25px',
        textShadow: `${HEADER_COLOR} 10px 10px 5px`,
        '@media screen and (max-width: 1024px)': {
            fontSize: '38px',
        },
        '@media screen and (max-width: 768px)': {
            fontSize: '32px',
            textShadow: `${HEADER_COLOR} 8px 8px 4px`,
        },
        '@media screen and (max-width: 550px)': {
            fontSize: '28px',
        },
        '@media screen and (max-width: 400px)': {
            fontSize: '24px',
            textShadow: `${HEADER_COLOR} 6px 6px 3px`,
        },
        pointerEvents: 'none',
    };

    const settings = {
        effect:'coverflow',
        grabCursor: true,
        centeredSlides: true,
        watchSlidesProgress: true,
        watchOverflow: true,
        loop: false,
        virtual: true,
        slidesPerView: is768px ? 2 : is1024px ? 3 : is1440px ? 4 : 6,
        initialSlide: index,
        spaceBetween: 30,
        slideToClickedSlide: true,
        threshold: 0,
        noSwipingSelector: 'input',
        speed: 300,
        mousewheel: {
            enabled: true,
            sensitivity: 3.5,
        },
        freeMode: {
            enabled: true,
            sticky: true,
            momentum: false,
        },
        keyboard: true,
        coverflowEffect: {
          rotate: 5,
          stretch: 0,
          depth: 100,
          modifier: is768px ? 2 : 3,
          slideShadows: false,
        },
        modules: [EffectCoverflow, Virtual, Mousewheel, Keyboard, FreeMode],
    };

    if (!photos.length) return <Box sx={noPhotosContainer}>
        <Typography sx={noPhotosTitle}>
            You have not uploaded any photos yet. To do that, click on the button located in the {is768px ? 'upper left corner.' : 'upper right corner or drag your photos here.' }
        </Typography>
    </Box>

    const setIndex = e => index = e.activeIndex;

    return (            
            <Box sx={imgMainContainer}>
                <PhotoSwipper {...settings} onSlideChange={setIndex} onDestroy={setIndex}>
                    {
                        photos?.map((photo, idx) => {
                            return <Box key={ uuid() }>
                            <PhotoSlide virtualIndex={idx}>
                                { ({isActive}) => {
                                    return <Photo isActive={isActive} id={photo.id} fullName={photo.name} url={photo.url} isDeleteMode={isDeleteMode} rerender={rerender}
                                    showRenamedAlert={showRenamedAlert} setNameOnError={setNameOnError} />
                                } }
                            </PhotoSlide>
                            </Box>
                        })
                    }
                </PhotoSwipper>
                <WarningAlert styles={{top: '4vh'}} text={`Failed to rename photo: most likely, you already have an image with the name: ${nameOnError}.`} from='left' needToShow={renamedTimeID !== null} />
            </Box>
    );
};