// redux-toolkit
import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    elems: {},
    selectedPhotosType: null,
    wasInitialized: false,
    isLoading: true,
    storageInfo: {quota: 0, usage: 0},
    isDeleteMode: false,
    filesCount: 0,
    foldersLength: [],
};

export const photosSlice = createSlice({
    name: 'photos',
    initialState,
    reducers: {
        setSelectedPhotosType: (state, action) => {
            state.selectedPhotosType = action.payload;
        },

        addNewPhotosType: (state, action) => {
            const type = action.payload;

            state.elems[type] = [];
        },

        addNewPhoto: (state, action) => {
            const photo = action.payload;

            state.elems[photo.belongsTo].push(photo);
        },

        renamePhoto: (state, action) => {
            const photo = action.payload;

            const foundIndex = state.elems[photo.belongsTo].findIndex(statePhoto => statePhoto.id === photo.id);

            state.elems[photo.belongsTo][foundIndex] = photo;
        },

        deletePhoto: (state, action) => {
            const {id, selectedPhotosType} = action.payload;

            const foundIndex = state.elems[selectedPhotosType].findIndex(photo => photo.id === id);

            state.elems[selectedPhotosType].splice(foundIndex, 1);
        },

        toggleWasInitialized: (state, action) => {
            state.wasInitialized = action.payload;
        },

        toggleIsLoading: (state, action) => {
            state.isLoading = action.payload;
        },

        initFolders: (state, action) => {
            const folders = action.payload;

            folders.forEach(folder => {
                state.elems[folder] = [];
            });
        },

        clearFolder: (state, action) => {
            const type = action.payload;
            let folder = state.elems[type];
            
            folder.splice(0, folder.length);
        },

        deleteFolder: (state, action) => {
            const name = action.payload;

            const folders = state.elems;

            delete folders[name];

            if (name === state.selectedPhotosType) {
                const keys = Object.keys(state.elems);

                state.selectedPhotosType = keys.length ? keys[keys.length - 1] : null;
            }

            if (Object.keys(folders).length === 0) {
                state.isDeleteMode = false;
            }
        },

        setStorageInfo: (state, action) => {
            if (state.storageInfo.usage === action.payload.usage) return;
            state.storageInfo = action.payload;
        },

        setDeleteMode: (state, action) => {
            state.isDeleteMode = action.payload;
        },

        setFilesCount: state => {
            const values = Object.values(state.elems);

            let wholeLength = 0;
    
            for (const value of values) {
                wholeLength += value.length;
            }
    
            state.filesCount = wholeLength;
        },

        setFoldersLength: state => {
            const folders = Object.entries(state.elems);

            const foldersLength = [];
    
            for (const [key, value] of folders) {
                foldersLength.push({name: key, length: value.length});
            }
    
            state.foldersLength = foldersLength;
        },
    },
});

export const {
    setActiveIndex,
    setSelectedPhoto, 
    setSelectedPhotosType, 
    addNewPhotosType,
    addNewPhoto,
    renamePhoto,
    deletePhoto,
    toggleWasInitialized,
    toggleIsLoading,
    initFolders,
    clearFolder,
    deleteFolder,
    setStorageInfo,
    setDeleteMode,
    setFilesCount,
    setFoldersLength,
} = photosSlice.actions;
export default photosSlice.reducer;