// react
import { useState } from "react";

// react-redux
import { useDispatch } from "react-redux";

// mui-components
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, TextField } from "@mui/material";

// utils
import { createObjectStore } from "../../utils/utils";

export const NewTypeWindow = ( {setIsClicked, addPhotosTypeBtnIsClicked, toggleVisibility, setMenuVisibility} ) => {
    const [storeName, setStoreName] = useState('');
    const dispatch = useDispatch();

    const handleClose = () => {
        setIsClicked(false);
        setStoreName('');
    };

    const onTextFieldChange = ({target}) => {
        setStoreName( target.value );
    };

    const handleActionsClick = ({target, currentTarget}) => {
        if (target.tagName !== 'BUTTON') return;

        const isCancelBtn = currentTarget.children[0] === target;
        const isAddBtn = currentTarget.children[1] === target;

        if (isCancelBtn) handleClose();
        
        if (isAddBtn && storeName.length) {
            const name = storeName.toLowerCase();

            createObjectStore({dispatch, setStoreName, handleClose, storeName: name, toggleVisibility, setMenuVisibility});
            setStoreName('');
        }

    };

    const onKeyUp = e => {
        if ( (e.code === 'Enter' && storeName.length) || (e.key === 'Enter' && storeName.length) ) {
            const name = storeName.toLowerCase();

            createObjectStore({dispatch, setStoreName, handleClose, storeName: name, toggleVisibility, setMenuVisibility});
            setStoreName('');
        }
    };

    return (
        <div>
            <Dialog open={addPhotosTypeBtnIsClicked} onClose={handleClose} onKeyUp={onKeyUp}>
                <DialogTitle>Add Folder</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Write folder name in input below.
                    </DialogContentText>
                    <TextField
                        onChange={onTextFieldChange}
                        placeholder='Type something...'
                        value={storeName}
                        autoFocus
                        margin="dense"
                        fullWidth
                        variant="standard"
                        error={storeName.length === 0}
                    />
                </DialogContent>
                <DialogActions onClick={handleActionsClick}>
                    <Button>Cancel</Button>
                    <Button>Add</Button>
                </DialogActions>
            </Dialog>
        </div>
    );
};