// constants
import { DB_NAME, NAME_INDEX, UPSET_FACE } from "../constants/constants";

// actions
import { 
    addNewPhoto, addNewPhotosType, deleteFolder, deletePhoto, initFolders, renamePhoto, setDeleteMode, setFilesCount, 
    setFoldersLength, setSelectedPhotosType, setStorageInfo, toggleIsLoading, toggleWasInitialized 
} from "../redux/slices/photosSlice";

// filesaver
import FileSaver from "file-saver";

// sweetalert
import swal from "sweetalert";

// JSzip
import JSZip from "jszip";

const fileToBlob = ({id, file}, selectedType) => {
    return {
        id,
        name: file.name,
        belongsTo: selectedType,
        url: URL.createObjectURL(file),
        length: file.size,
    }
};

export const getStorageInfo = (dispatch, setStorageInfo) => {
    if (navigator) {
        if (navigator.storage) {
            if (navigator.storage.estimate) {
                navigator.storage.estimate()
                .then(data => ({quota: data.quota, usage: data.usage}))
                .then(result => dispatch( setStorageInfo(result) ));
            }
        }
    }
};

export const initApp = ({wasInitialized, dispatch}) => {
    if (wasInitialized) return;
    const req = indexedDB.open(DB_NAME);

    req.onsuccess = function(e) {
        const db = e.target.result;
        let counter = 0;

        const stores = db.objectStoreNames;
        const storeNames = Object.values(stores);

        if (storeNames.length) {
            dispatch( toggleIsLoading(true) );
        } else {
            setTimeout(() => dispatch( toggleIsLoading(false) ), 100);
        }

        dispatch( initFolders(storeNames) );

        for (let i = 0; i < storeNames.length; i++) {
            const name = storeNames[i];

            const transaction = db.transaction(name, "readonly");

            const store = transaction.objectStore(name);

            const request = store.openCursor();

            request.onsuccess = function() {
                const cursor = request.result;
                if (cursor) {
                    const {id, file} = cursor.value;
                    dispatch( addNewPhoto( fileToBlob({id, file}, name) ) );
                    cursor.continue();
                } else {
                    dispatch( toggleWasInitialized(true) );
                }                  
            }

            transaction.onabort = () => {
                swal({
                    title: "Oops!",
                    text: `Failed to initialize the app: transaction was denied`,
                    icon: "error",
                    button: `Oh, OK ${UPSET_FACE}`,
                });

                db.close();
            };

            transaction.oncomplete = () => {
                counter++;
                if (storeNames.length === counter) {
                    dispatch( setFoldersLength() );
                    dispatch( setFilesCount() );

                    setTimeout(() => dispatch( toggleIsLoading(false) ), 100);
                    
                    getStorageInfo(dispatch, setStorageInfo);
                    
                    setInterval(() => getStorageInfo(dispatch, setStorageInfo), 1500);

                    db.close();
                }
            };
        }

        db.close();
    }

    req.onerror = () => {
        swal({
            title: "Oops!",
            text: `Failed to connect: connection was lost.`,
            icon: "error",
            button: `Oh, OK ${UPSET_FACE}`,
        });
    };
};

export const createObjectStore = ({dispatch, setStoreName, handleClose, storeName, toggleVisibility, setMenuVisibility}) => {
    const request = indexedDB.open(DB_NAME);
    
    request.onsuccess = function (e){
        const database = e.target.result;
        const version = parseInt(database.version);

        database.close();

        const secondRequest = indexedDB.open(DB_NAME, version+1);

        secondRequest.onupgradeneeded = function (e) {
            const database = e.target.result;

            if (!database.objectStoreNames.contains(storeName)) {
                const store = database.createObjectStore(storeName, {
                    keyPath: 'id', autoIncrement: true
                });

                store.createIndex(NAME_INDEX, "file.name", { unique: true });

                dispatch( addNewPhotosType( storeName ) );
                dispatch( setSelectedPhotosType( storeName ) );
                setStoreName('');
                handleClose();
                toggleVisibility(false);
                setMenuVisibility(false);
            } else {
                swal({
                    title: "Oops!",
                    text: `You already have a folder named "${storeName}". Please, choose another name.`,
                    icon: "error",
                    button: `Oh, OK ${UPSET_FACE}`,
                  });
            }

            database.close();
        };
    }
};

export const uploadImage = ({photos, dispatch, selectedPhotosType, showAlreadyUploadedAlert, setAlreadyUploadedText}) => {
    if (!selectedPhotosType) return;

    const req = indexedDB.open(DB_NAME);

    req.onsuccess = function (e) {
        const db = e.target.result;

        const transaction = db.transaction(selectedPhotosType, "readwrite");

        let needToBeAdded = [];
        let alreadyUploaded = [];

        transaction.oncomplete = () => {
            for (let i = 0; i < needToBeAdded.length; i++) {
                dispatch( addNewPhoto( fileToBlob(needToBeAdded[i], selectedPhotosType) ) );
            }

            dispatch( setFilesCount() );
            dispatch( setFoldersLength() );

            const text = `You already have ${alreadyUploaded.length > 1 ? 'images with the following names: ' + alreadyUploaded.join(', ') : 'an image with the following name: ' + alreadyUploaded[0]}`;
                    
            if (alreadyUploaded.length) {
                setAlreadyUploadedText(text);
                showAlreadyUploadedAlert();
            }

            alreadyUploaded = [];
            needToBeAdded = [];

            db.close();
        };

        transaction.onabort = () => {
            swal({
                title: "Oops!",
                text: 'Adding photo to the store was failed. Something went wrong...',
                icon: "error",
                button: `Oh, OK ${UPSET_FACE}`,
            });
            
            db.close();
        }

        const store = transaction.objectStore(selectedPhotosType);

        const nameIndex = store.index(NAME_INDEX);

        for (let i = 0; i < photos.length; i++) {
            const req = nameIndex.get(photos[i].name);

            req.onsuccess = function(e) {
                const result = e.target.result;

                if (result) {
                    const {file} = result;
                    alreadyUploaded.push(file.name);
                } else {
                    const added = store.add({file: photos[i]});

                    added.onsuccess = function(e) {
                        const id = e.target.result;
                        if (id) {
                            needToBeAdded.push({id, file: photos[i]});
                        }
                    }
                }
            }
        }

        db.close();
    }

    req.onerror = () => {
        swal({
            title: "Oops!",
            text: `Failed to connect: connection was lost.`,
            icon: "error",
            button: `Oh, OK ${UPSET_FACE}`,
        });
    };
};

export const deleteObjectStore = ({folder, dispatch}) => {
    const request = indexedDB.open(DB_NAME);
    
    request.onsuccess = function (e){
        const db = e.target.result;
        const version = parseInt(db.version);

        const transaction = db.transaction(folder, "readwrite");
        const store = transaction.objectStore(folder);

        store.clear();

        transaction.oncomplete = () => {
            const secondReq = indexedDB.open(DB_NAME, version + 1);

            secondReq.onupgradeneeded = function(e) {
                const db = e.target.result;
                
                db.deleteObjectStore(folder);

                dispatch( deleteFolder(folder) );
                
                db.close();
            }
        };

        transaction.onabort = () => {
            swal({
                title: "Oops!",
                text: `Failed to delete the folder: something went wrong...`,
                icon: "error",
                button: `Oh, OK ${UPSET_FACE}`,
            });

            db.close();
        };

        db.close();
    }

    request.onerror = () => {
        swal({
            title: "Oops!",
            text: `Failed to connect: connection was lost.`,
            icon: "error",
            button: `Oh, OK ${UPSET_FACE}`,
        });
    };
};

export const renamePhotoFromObjectStore = ({newName, storeName, dispatch, id, setNameOnError, showRenamedAlert}) => {
    const request = indexedDB.open(DB_NAME);
        
    request.onsuccess = function (e){
        const database = e.target.result;

        const transaction = database.transaction(storeName, "readwrite"); 

        const store = transaction.objectStore(storeName);

        const req = store.openCursor(id);

        req.onsuccess = function(e) {
            const cursor = e.target.result;

            if (cursor) {
                const {file, id} = cursor.value;
                const updateData = cursor.value;

                const newFileName = `${newName + file.name.slice(file.name.indexOf('.'))}`;

                const blob = new Blob([file], {type: file.type});

                const newFile = new File([blob], newFileName, {
                    type: file.type,
                });

                updateData.file = newFile;

                const request = cursor.update(updateData);

                request.onsuccess = e => {
                  if (e.target.result === id) {

                    transaction.oncomplete = () => {
                        dispatch( renamePhoto( fileToBlob(updateData, storeName) ) );
                        database.close();
                    };
                  }
                }

                request.onerror = () => {
                    setNameOnError(newFileName);

                    showRenamedAlert();
                };
            }
        }

        database.close();
    }

    request.onerror = () => {
        swal({
            title: "Oops!",
            text: `Failed to connect: connection was lost.`,
            icon: "error",
            button: `Oh, OK ${UPSET_FACE}`,
        });
    };
};

export const deleteFromObjectStore = ({id, dispatch, selectedPhotosType}) => {
    const req = indexedDB.open(DB_NAME);

    req.onsuccess = function(e) {
        const db = e.target.result;

        const transaction = db.transaction(selectedPhotosType, "readwrite");

        const store = transaction.objectStore(selectedPhotosType);

        const foundReq = store.openCursor(id);

        foundReq.onsuccess = function(e) {
            const cursor = e.target.result;

            if (cursor) {
                const request = cursor.delete();
                request.onsuccess = e => {
                    if (!e.target.result) {
                        dispatch( deletePhoto({id, selectedPhotosType}) );

                        const countReq = store.count();

                        countReq.onsuccess = function(e) {
                            if (e.target.result === 0) {
                                dispatch( setDeleteMode(false) );
                            }
                        }

                        dispatch( setFilesCount() );
                        dispatch( setFoldersLength() );                      
                    }
                }
            }
        }

        db.close();
    }

    req.onerror = () => {
        swal({
            title: "Oops!",
            text: `Failed to connect: connection was lost.`,
            icon: "error",
            button: `Oh, OK ${UPSET_FACE}`,
        });
    };
};

export const downloadObjectStore = ({folder}) => {
    const req = indexedDB.open(DB_NAME);

    req.onsuccess = function() {
        const db = req.result;

        const transaction = db.transaction(folder, "readonly");

        const store = transaction.objectStore(folder);

        const zip = new JSZip();
        
        const zipFolder = zip.folder(folder);

        const countReq = store.count();

        countReq.onsuccess = function(e) {
            if (e.target.result === 0) {
                transaction.abort();
                return;
            }

            const request = store.openCursor();

            request.onsuccess = function() {
                const cursor = request.result;
                if (cursor) {
                    const {file} = cursor.value;

                    zipFolder.file(file.name, file);
                    
                    cursor.continue();
                } else {
                    zip.generateAsync({ type: 'blob' }).then(function (content) {
                        FileSaver.saveAs(content, 'photos.zip');
                    });
                }
            }
        }

        db.close();
    }

    req.onerror = () => {
        swal({
            title: "Oops!",
            text: `Failed to connect: connection was lost.`,
            icon: "error",
            button: `Oh, OK ${UPSET_FACE}`,
        });
    };
};

export const downloadAllObjectStores = () => {
    const req = indexedDB.open(DB_NAME);

    req.onsuccess = function() {
        const db = req.result;

        const storeNames = Object.values(db.objectStoreNames);

        let counter = 0;

        const zip = new JSZip();

        for (let i = 0; i < storeNames.length; i++) {
            const name = storeNames[i];

            const transaction = db.transaction(name, 'readonly');

            const store = transaction.objectStore(name);

            const countReq = store.count();

            countReq.onsuccess = function(e) {
            if (e.target.result === 0) {
                return;
            }

            const folder = zip.folder(name);

            const request = store.openCursor();

            request.onsuccess = function() {
                const cursor = request.result;

                if (cursor) {
                    const {file} = cursor.value;

                    folder.file(file.name, file);

                    cursor.continue();
                }
          }
        }

        transaction.oncomplete = () => {
          counter++;

          if (counter === storeNames.length) {
            zip.generateAsync({ type: 'blob' }).then(function (content) {
              FileSaver.saveAs(content, 'all_photos.zip');
            });

            db.close();
          }
        }
      }

      db.close();
    }

    req.onerror = () => {
        swal({
            title: "Oops!",
            text: `Failed to connect: connection was lost.`,
            icon: "error",
            button: `Oh, OK ${UPSET_FACE}`,
        });
    };
};