// images
import createFolders from './images/Create_folders.jpg';
import addOrDelete from './images/Add_or_delete_photos.jpg';
import renamePhotos from './images/Rename_photos.jpg';
import memoryCheck from './images/Check_memory_usage.jpg';
import downloadOrDelete from './images/Download_or_delete_folder.jpg';

// react
import { useEffect, useState } from 'react';

// react-redux
import { useDispatch, useSelector } from 'react-redux';

// custom hooks
import { useDelay } from './hooks/useDelay';

// components
import { Header } from './components/Header/Header';
import { MainContent } from './components/MainContent/MainContent';
import { Loader } from './components/Loader/Loader';
import { WarningAlert } from './components/WarningAlert/WarningAlert';

// mui-components
import { Box, Typography } from '@mui/material';

// mui-icons
import UploadFileRoundedIcon from '@mui/icons-material/UploadFileRounded';

// utils
import { uploadImage } from './utils/utils';

// constants
import { GRINNING_FACE, HAPPY_FACE, HEADER_COLOR } from './constants/constants';

// sweetalert
import Swal from 'sweetalert2';

// styles
import './App.css';
import '@fontsource/roboto/700.css';

export const App = () => {
  const dispatch = useDispatch();
  const selectedPhotosType = useSelector(state => state.photos.selectedPhotosType);
  const isLoading = useSelector(state => state.photos.isLoading);

  const [drag, setDrag] = useState(false);

  const [filesLength, setLength] = useState(0);

  const [safariTimeID, showSafariAlert] = useDelay(7500);
  const [droppedTimeID, showDroppedAlert] = useDelay(7500);
  const [alreadyUploadedTimeID, showAlreadyUploadedAlert] = useDelay(7500);

  const [alreadyUploadedText, setAlreadyUploadedText] = useState('');

  const onDragTrue = e => {
    e.preventDefault();
    setDrag(true);
  };

  const onDragFalse = e => {
    e.preventDefault();
    if (e.relatedTarget === null) setDrag(false);
  };

  const onDrop = e => {
    e.preventDefault();
    setDrag(false);
    setLength(0);

    if (!selectedPhotosType) return;

    let files = Object.values(e.dataTransfer.files);

    const filesLength = files.length;

    // if an image
    const filtered = files.filter(file => file.type.split('/')[0].toLowerCase() === 'image');

    const filteredLength = filtered.length;

    const result = [];

    // remove copies from the uploaded images
    for (let i = 0; i < filtered.length; i++) {
        const found = result.find(file => file.name === filtered[i].name);

        if (found) continue;

        result.push(filtered[i]);
    }

    if (result.length === 0) {
      showDroppedAlert();
      setLength(filesLength - filteredLength);
      return;
    }

    uploadImage({photos: result, dispatch, selectedPhotosType, showAlreadyUploadedAlert, setAlreadyUploadedText});
  };

  const uploadedContainer = {
    position: 'absolute',
    top: '0',
    left: '0',
    width: '100%',
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    background: '#efefef',
    opacity: '.7',
    zIndex: '99999',
  };

  const uploadIcon = {
    fontSize: '70vh',
    pointerEvents: 'none',
    color: '#555',
  };

useEffect(() => {
  if (!isLoading) return;
  if ( !localStorage.getItem('Introduced') ) {
    setTimeout(() => {
      (async function() {
        const steps = ['1', '2', '3', '4', '5'];
  
        const Queue = Swal.mixin({
          progressSteps: steps,
        });
  
        await Queue.fire({
          customClass: 'swal-first-show',
          html: ` <img src="${createFolders}" class='swal-image' />
          <p class='swal-description-text'>Create folders to store your favourite photos.</p> `,
          currentProgressStep: 0,
          confirmButtonText: `Next ${GRINNING_FACE}`,
        });
  
        await Queue.fire({
          customClass: 'swal-first-show',
          html: ` <img src="${addOrDelete}" class='swal-image' />
          <p class='swal-description-text'>Swipe over your photos for scroll or click to the one you want to see. Turn the mousewheel and hold down the keys "Left" and "Right" arrows for faster scroll.
          One press of the keys will allow you to switch your photos smoothly. Also delete your photos using the "delete" mode.</p> `,
          currentProgressStep: 1,
          confirmButtonText: `Next ${GRINNING_FACE}`,
        });
  
        await Queue.fire({
          customClass: 'swal-first-show',
          html: ` <img src="${renamePhotos}" class='swal-image' />
          <p class='swal-description-text'>Rename your photos the way you like.</p> `,
          currentProgressStep: 2,
          confirmButtonText: `Next ${GRINNING_FACE}`,
        });
  
        await Queue.fire({
          customClass: 'swal-first-show',
          html: ` <img src="${memoryCheck}" class='swal-image' />
          <p class='swal-description-text'>Check how much memory your photos take up and what is your storage memory limit.</p> `,
          currentProgressStep: 3,
          confirmButtonText: `Next ${GRINNING_FACE}`,
        });
  
        await Queue.fire({
          customClass: 'swal-first-show',
          html: ` <img src="${downloadOrDelete}" class='swal-image' />
          <p class='swal-description-text'>Download folders in the zip format or delete them if it is necessary.</p> `,
          currentProgressStep: 4,
          confirmButtonText: `Thanks, got it! ${HAPPY_FACE}`,
        });

        showSafariAlert();

        localStorage.setItem('Introduced', true);
      })();
    }, 250);
  }
}, []);

  const noDBTitle = {
    position: 'absolute',
    color: 'white',
    textAlign: 'center',
    fontSize: '48px',
    top: '50vh',
    transform: 'translateY(-50%)',
    width: 'calc(100% - 50px)',
    padding: '0 25px',
    textShadow: `${HEADER_COLOR} 10px 10px 5px`,
    '@media screen and (max-width: 1024px)': {
        fontSize: '38px',
    },
    '@media screen and (max-width: 768px)': {
        fontSize: '32px',
        textShadow: `${HEADER_COLOR} 8px 8px 4px`,
    },
    '@media screen and (max-width: 550px)': {
        fontSize: '28px',
    },
    '@media screen and (max-width: 400px)': {
        fontSize: '24px',
        textShadow: `${HEADER_COLOR} 6px 6px 3px`,
    }
};

const moreThan1 = filesLength > 1;

if (!window.indexedDB) {
    return <Box className="app">
      <Typography sx={noDBTitle} variant='h2' component='h3' color='white'>Sorry, this app uses IndexedDB to store photos, but it is not available. :(</Typography>
    </Box>
}

  return (
    <Box className="App" onDragStart={e => e.preventDefault()} onDragOver={onDragTrue} onDragEnter={onDragTrue} onDragLeave={onDragFalse} onDrop={onDrop}>
      <Header />
      <MainContent />
      { isLoading && <Loader /> }
      { drag && selectedPhotosType ? (
          <Box sx={uploadedContainer}>
            <UploadFileRoundedIcon sx={uploadIcon} />
          </Box>
        ) : null }
        <WarningAlert text='The app may not work correctly in the Safari browser' needToShow={safariTimeID !== null} from='left' />
        <WarningAlert text={`Failed to upload: ${moreThan1 ? 'files' : 'file'} may not be ${moreThan1 ? 'images' : 'an image'} or you tried to upload an image shortcut`} needToShow={droppedTimeID !== null} from='left' />
        <WarningAlert text={alreadyUploadedText} from='left' needToShow={alreadyUploadedTimeID !== null} />
    </Box>
  );
}
