// mui-components
import { Box } from '@mui/system';

// mui-font
import '@fontsource/roboto/700.css';

export const Loader = () => {
    const waterContainer = {
        width: '100vw',
        height: '100vh',
        backgroundColor: '#333',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column',
        position: 'fixed',
        top: '0',
        left: '0',
        zIndex: '999',
    };

    const water = {
        width: '320px',
        height: '320px',
        backgroundColor: 'skyblue',
        borderRadius: '160px',
        position: 'relative',
        boxShadow: 'inset 0 0 24px 0 rgba(0,0,0,.5), 0 3px 8px 0 rgba(0,0,0,.5)',
        overflow: 'hidden',
        ':before': {
            content:`''`,
            position: 'absolute',
            width:'320px',
            height: '320px',
            top:'-150px',
            backgroundColor: '#fff',
            borderRadius: '45%',
            background: 'rgba(255,255,255,.7)',
            animation: 'wave 5s linear infinite',
        },
        ':after': {
            content:`''`,
            position: 'absolute',
            width:'320px',
            height: '320px',
            top:'-150px',
            backgroundColor: '#fff',
            borderRadius: '35%',
            background: 'rgba(255,255,255,.3)',
            animation: 'wave 5s linear infinite',
        },
        '@keyframes wave': {
            '0%': {
                transform: 'rotate(0deg)',
            },
            '100%': {
                transform: 'rotate(360deg)',
            },
        },
        '@media screen and (max-width: 1440px)': {
            transform: 'scale(0.8)',
        },
        '@media screen and (max-width: 768px)': {
            transform: 'scale(0.65)',
        },
        '@media screen and (max-width: 500px)': {
            transform: 'scale(0.5)',
        },
    };

    return <Box sx={waterContainer}>
        <Box sx={water}></Box>
    </Box>
};