// react
import { Component } from "react";

// mui-components
import { Box, Typography } from "@mui/material";

// mui-styled
import styled from "@emotion/styled";

// icon
import man from '../../icons/man.png';

// constants
import { LIGHT_GRAY } from "../../constants/constants";

export class ErrorBoundary extends Component {
    constructor(props) {
        super(props);
        this.state = { hasError: false };
    }
  
    static getDerivedStateFromError() {
        return { hasError: true };
    }

    render() {

        const errorContainer = {
            position: 'relative',
            top: '0',
            left: '0',
            width: '100vw',
            height: 'calc(100vh - 10px)',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            margin: '5px 0',
            padding: '0',
        };

        const errorText = {
            color: LIGHT_GRAY,
            textAlign: 'center',
            padding: '0 50px',
            fontSize: '32px',
            "@media screen and (max-width: 1366px)": {
                fontSize: '28px',
            },
            "@media screen and (max-width: 1024px)": {
                fontSize: '24px',
            },
            "@media screen and (max-width: 768px)": {
                fontSize: '20px',
            },
            "@media screen and (max-width: 415px)": {
                fontSize: '16px',
            },
            "@media screen and (max-height: 550px)": {
                fontSize: '18px',
            },
            "@media screen and (max-height: 400px)": {
                fontSize: '14px',
            }
        };

        const Image = styled('img')({
            width: '225px',
            marginBottom: '15px',
            "@media screen and (max-width: 1366px)": {
                width: '200px',
            },
            "@media screen and (max-width: 1024px)": {
                width: '175px',
            },
            "@media screen and (max-width: 768px)": {
                width: '150px',
            },
            "@media screen and (max-width: 415px)": {
                width: '130px',
            },
            "@media screen and (max-height: 550px)": {
                width: '140px',
            },
            "@media screen and (max-height: 400px)": {
                width: '120px',
            }
        });

        if (this.state.hasError) {
            return <Box sx={errorContainer}>
                <Image src={man} />
                <Typography sx={errorText} component='h4' variant='h4'>Oops... I am very sorry, but some error has occurred in the app and it will not work. 
                Please try to reload the page or contact the developer.</Typography>
            </Box>
        }
  
        return this.props.children; 
    }
};