// react-redux
import { useSelector } from "react-redux";

// components
import { Photos } from "../Photos/Photos";
import { MainInfo } from "../MainInfo/MainInfo";

// mui-components
import { Box } from "@mui/material";

export const MainContent = () => {
    const selectedPhotosType = useSelector(state => state.photos.selectedPhotosType);

    const main = {
      position: 'relative',
      top: '0',
      left: '0',
      margin: '0',
    };

    return (
        <Box component='main' sx={main}>
            {
                selectedPhotosType ? <Photos /> : <MainInfo />
            }
        </Box>
    )
};